                  class="price-ico"
                />
                <span>{{ i.price }}</span>
              </p>
              <span v-show="i.price == 0" class="free">免费</span>
            </div>
          </li>
        </ul>
      </div>

      <!-- 明星讲师 -->
      <div class="mingxing">
        <div class="list_title">
          <p class="red_title"></p>
          <span>明星讲师</span>
        </div>
        <ul>
          <li @click="toTeacher(i)" v-for="(i, k) in mxjs" :key="k">
            <img :src="i.teacher_avatar" alt="" />
            <div>
              <p>
                <span>{{ i.teacher_name }}</span
                ><span>M11</span>
              </p>
              <p>
                {{ i.introduction }}
              </p>
            </div>
          </li>
        </ul>
      </div>
